@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  font-family: Poppins;
  --primary-color: #137186;
  --secondary-color: #0c358d;
  --nav-color: white;
  --font-color: black;
  --off-white-color: rgb(197, 187, 187);
}

.theme-profit {
  --primary-color: #064e3b;
  --secondary-color: #038b37;
  --nav-color: white;
  --font-color: black;
  --off-white-color: rgb(197, 187, 187);
}

.theme-loss {
  --primary-color: #831212;
  --secondary-color: #f86f14;
  --nav-color: white;
  --font-color: black;
  --off-white-color: rgb(197, 187, 187);
}

body {
  margin: 0px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main {
  width: 95%;
  max-width: 30rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  background-color: var(--primary-color);
  border-radius: 10px;
  margin: auto;
  box-shadow: 0 2px 2px 0 grey, 0 3px 1px -2px grey, 0 1px 5px 0 grey;
}

.main h1 {
  text-align: center;
}

.main input {
  display: block;
  background-color: var(--nav-color);
  border-radius: 4px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: auto;
  margin: 1rem 0px;
  font-size: 1.7rem;
  padding: 8px;
  border: none;
  text-align: center;
}

.main input[type="text"] {
  max-width: 14.7rem;
}

.main button {
  width: 20%;
  background-color: var(--nav-color);
  border-radius: 4px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: auto;
  font-size: 1.8rem;
  margin: 1rem;
  font-weight: bold;
  padding: 10px;
  border: none;
  color: var(--primary-color);
}

.main button:hover {
  color: var(--secondary-color);
}

.invalid-input {
  background-color: var(--nav-color);
  padding: 0px 20px;
  margin: auto;
  border-radius: 4px;
  max-width: 500px;
  margin: auto;
}

.invalid-input h4 {
  color: red;
  text-align: center;
  font-weight: bold;
}

.graph {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.graph h4 {
  text-align: center;
  font-size: 1rem;
  margin: 0px;
  word-wrap: break-word;
}

.graph .profit__div {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.graph .profit-box {
  background-color: var(--secondary-color);
  border-radius: 0.2rem;
  margin: 1rem auto;
  padding: 0.5rem;
  width: 9rem;
}

.graph img {
  width: 39%;
  padding: 1rem;
}

